const initialState = {
    menu : []
}
function menuacces(state = initialState, action){

    switch(action.type){
        case "MenuAccess":
            let stateuser = {...state}
            if(action.payload){
                stateuser.menu = action.payload.data;
                
               
            }
            return {...stateuser}
        default:
            return state;
    }
}
export default menuacces;